<template>
    <div id="Buttons">
        <div class="button-box">
            <h3 class="margin-bottom10"></h3>
            <div class="padding-10">
                <Button></Button>
                <Button type="primary" size="large"></Button>
                <Button type="dashed" size="small"></Button>
                <Button type="text"></Button>
                <br><br>
                <Button type="info"></Button>
                <Button type="success"></Button>
                <Button type="warning"></Button>
                <Button type="error"></Button>
            </div>
            <h3 class="margin-bottom10 margin-top20">ghost</h3>
            <div class="ghost-back padding-10">
                <Button ghost></Button>
                <Button type="primary" size="large" ghost></Button>
                <Button type="dashed" size="small" ghost></Button>
                <Button type="text" ghost></Button>
                <br><br>
                <Button type="info" ghost></Button>
                <Button type="success" ghost></Button>
                <Button type="warning" ghost></Button>
                <Button type="error" ghost></Button>
            </div>
            <h3 class="margin-bottom10 margin-top10">disabled</h3>
            <div class="padding-10">
                <Button type="info" disabled></Button>
                <Button type="success" disabled></Button>
                <Button type="warning" disabled ghost></Button>
                <Button type="error" disabled ghost></Button>
            </div>
        </div>
        <div>
            <h3 class="margin-bottom10 margin-top20"></h3>
            <div class="padding-10">
                <Button type="primary" icon="ios-search"></Button>
                <Button type="primary" shape="circle" icon="ios-search"></Button>
                <Button type="primary" disabled icon="ios-search"></Button>
            </div>
            <h3 class="margin-bottom10"></h3>
            <div class="padding-10">
                <Button type="primary" @click="openModal('info')">info</Button>
                <Button @click="openModal('success')">Success</Button>
                <Button @click="openModal('warning')">Warning</Button>
                <Button @click="openModal('error')">Error</Button>
            </div>
            <div class="padding-10">
                <Button @click="openModal('success',2)">Success2</Button>
                <Button @click="openModal('error',2)">Error2</Button>
            </div>
            <div class="padding-10">
                <Button @click="openModal('success',3)">Success3</Button>
                <Button @click="openModal('error',3)">Error3</Button>
            </div>
            <h3 class="margin-bottom10">confirm</h3>
            <div class="padding-10">
                <Button @click="openConfirm('edit')"></Button>
                <Button @click="openConfirm('del')"></Button>
            </div>
        </div>
        <div>
            <h3 class="margin-bottom10"></h3>
            <div class="padding-10">
                <div class="dis-inline">
                    <CWDropDown type="primary"></CWDropDown>
                </div>
                <div class="dis-inline">
                    <CWDropDown :search="true" class="margin-top10"></CWDropDown>
                </div>
            </div>
            <h3 class="margin-bottom10 margin-top20"></h3>
            <div class="padding-10">
                <ButtonGroup class="CW-group-buttons">
                    <Button type="info">1</Button>
                    <Button type="info">2</Button>
                    <Button type="info">3</Button>
                </ButtonGroup>
            </div>
            <div class="padding-10">
                <ButtonGroup class="CW-group-ghost">
                    <Button type="info" ghost>1</Button>
                    <Button type="info" ghost>2</Button>
                    <Button type="info" ghost>3</Button>
                </ButtonGroup>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Buttons',
        methods: {
            openModal(type, style) {
                this.$CWMessage({
                    title: '',
                    content: '',
                    type: type,
                    okText: '',
                    style: style
                });
            },
            openConfirm(type) {
                this.$CWConfirm({
                    type: type,
                    title: '',
                    content: '',
                    okText: '',
                    cancelText: '',
                }).then(() => {
                    console.log('')
                }).catch(() => {
                    console.log('')
                });
            }
        }
    }
</script>

<style scoped lang="scss">
    #Buttons {
        display: flex;
        justify-content: space-between;
        .ghost-back {
            background: $split-color;
        }
    }
</style>



